async function getLuckyDrawAmount(){
    const web3 = new Web3("https://bsc-dataseed1.defibit.io/");
    const luckyContract = new web3.eth.Contract(Lucky.abi, luckyAddress);
    let luckyDrawAmount = await luckyContract.methods.luckyDrawAmount().call();
    let amount = web3.utils.fromWei(luckyDrawAmount.toString());
    return parseInt(amount, 10).toFixed(2);
}

